<script setup>
import { ref, computed, onMounted } from 'vue';
import { useUserStore } from '@/stores/user';
import { userApi } from '@/api/user';  // 导入userApi
import {
  Bicycle, Wallet, Discount, ArrowRight, 
  Right, Star, Timer, Location, StarFilled,
  Warning, User, DataLine, List, UserFilled,
  Lock, Message, Iphone, Key, Connection
} from '@element-plus/icons-vue';

const userStore = useUserStore();

// 进度条格式化函数
const format = (percentage) => {
  return `${percentage}/100`;
};

// 计算会员等级和进度
const memberLevel = computed(() => {
  const points = userStore.user?.statistics?.points || 0; // 获取真实积分，默认为0
  if (points >= 200) return '黄金会员';
  if (points >= 100) return '白银会员';
  return '普通会员';
});

const memberProgress = computed(() => {
  const points = userStore.user?.statistics?.points || 0;
  if (points >= 200) return 100;
  if (points >= 100) return points / 2;
  return points;
});

const nextLevelPoints = computed(() => {
  const points = userStore.user?.statistics?.points || 0;
  if (points >= 200) return 0;
  if (points >= 100) return 200 - points;
  return 100 - points;
});

// 格式化日期
const formatDate = (dateString) => {
  if (!dateString) return '未知';
  try {
    // 支持多种日期格式
    const date = new Date(dateString);
    
    // 检查日期是否有效
    if (isNaN(date.getTime())) {
      console.warn('无效的日期格式:', dateString);
      return dateString;
    }
    
    // 格式化为YYYY-MM-DD
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    
    return `${year}-${month}-${day}`;
  } catch (e) {
    console.error('日期格式化错误:', e);
    return dateString;
  }
};

// 格式化骑行时长（分钟转为时分格式）
const formatDuration = (minutes) => {
  if (!minutes && minutes !== 0) return '未知';
  
  const hours = Math.floor(minutes / 60);
  const mins = Math.floor(minutes % 60);
  
  if (hours > 0) {
    return `${hours}小时${mins > 0 ? ` ${mins}分钟` : ''}`;
  }
  return `${mins}分钟`;
};

// 计算平均骑行距离
const calculateAvgDistance = () => {
  const totalDistance = userStore.user?.statistics?.totalDistance || 0;
  const ridesCount = userStore.user?.statistics?.ridesCount || 0;
  
  if (ridesCount === 0) return '0.0';
  
  const avgDistance = totalDistance / ridesCount;
  return avgDistance.toFixed(1); // 保留一位小数
};

// 加载用户信息
const loadUserData = async () => {
  try {
    const response = await userApi.getProfile();
    console.log('获取到的用户信息:', response?.data);
    
    if (response?.data?.success && response.data.user) {
      // 更新用户存储
      const userData = {
        ...userStore.user,
        ...response.data.user
      };
      userStore.setUser(userData);
      console.log('用户信息已更新:', userData);
    }
  } catch (error) {
    console.error('加载用户信息失败:', error);
  }
};

// 在组件挂载时加载用户信息
onMounted(() => {
  // 加载最新用户信息
  loadUserData();
});
</script>

